import React from 'react'
import SideBar from "../components/SideBar";
import ComingSoon from "../assets/ComingSoon1.png";
function Planner() {
    return (
        <div className='dash'>
            <SideBar />
            <div className="div">
                <img src={ComingSoon} alt="" />
            </div>
        </div>
    )
}

export default Planner
