import React, { useState } from 'react';
import SideBar from "../components/SideBar";
import './tracker.css'; // Import the CSS file

function Tracker() {
    // Define state for the current date
    const [currentDate, setCurrentDate] = useState(new Date());

    // Function to get the days in a month
    const getDaysInMonth = (date) => {
        const year = date.getFullYear();
        const month = date.getMonth();
        const daysInMonth = new Date(year, month + 1, 0).getDate();
        return Array.from({ length: daysInMonth }, (_, index) => index + 1);
    };

    // Function to get the name of the month
    const getMonthName = (date) => {
        return date.toLocaleString('default', { month: 'long' });
    };

    // Function to handle changing the month
    const changeMonth = (increment) => {
        setCurrentDate(prevDate => {
            const newDate = new Date(prevDate);
            newDate.setMonth(newDate.getMonth() + increment);
            return newDate;
        });
    };

    return (
        <div className="calendar-container">
            <SideBar />
            <div className="calendar">
                <div className="calendar-header">
                    <button onClick={() => changeMonth(-1)}>Prev</button>
                    <h2>{getMonthName(currentDate)}</h2>
                    <button onClick={() => changeMonth(1)}>Next</button>
                </div>
                <div className="calendar-days">
                    {getDaysInMonth(currentDate).map(day => (
                        <div key={day} className="calendar-day">{day}</div>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default Tracker;
