import React from 'react';
import { useNavigate } from 'react-router-dom';
import './Sidebar.css'

const SideBar = () => {
  const navigate = useNavigate();

  const navigateToDashboard = () => {
    navigate('/Dashboard');
  };

  const navigateToPlanner = () => {
    navigate('/Dashboard/Planner');
  };

  const navigateToTracker = () => {
    navigate('/Dashboard/Tracker');
  };

  const navigateToExplore = () => {
    navigate('/Dashboard/Explore');
  };

  return (
    <div className="sidebar">
      <p>Logo</p>
      <button onClick={navigateToDashboard}>Dashboard</button>
      <button onClick={navigateToPlanner}>Planner</button>
      <button onClick={navigateToTracker}>Tracker</button>
      <button onClick={navigateToExplore}>Explore</button>
    </div>
  );
}

export default SideBar;
