// TodoList.js

import React, { useState } from 'react';
import './TodoList.css';

const TodoList = () => {
    const [tasks, setTasks] = useState([
        { id: 1, text: "Work out twice a day for 45 minutes each", completed: false },
        { id: 2, text: "Drink 1 gallon (3.7 liters) of water daily", completed: false },
        { id: 3, text: "Read 10 pages of a non-fiction book daily", completed: false },
        { id: 4, text: "Follow a diet with no cheat meals", completed: false },
        { id: 5, text: "Take a progress picture every day", completed: false },
        // Add more tasks as needed
    ]);

    const handleCheckboxChange = (taskId) => {
        const updatedTasks = tasks.map(task => {
            if (task.id === taskId) {
                return { ...task, completed: !task.completed };
            }
            return task;
        });
        setTasks(updatedTasks);
    };

    return (
        <div className="todo-list">
            <h2>75 Hard Challenge Tasks</h2>
            <ul>
                {tasks.map(task => (
                    <li key={task.id} className={task.completed ? 'completed' : ''}>
                        <input
                            type="checkbox"
                            checked={task.completed}
                            onChange={() => handleCheckboxChange(task.id)}
                        />
                        <span>{task.text}</span>
                    </li>
                ))}
            </ul>
        </div>
    );
}

export default TodoList;
